  <div class="regiset">
    <div class="put">
      <span></span>
      <input type="text" placeholder="" v-model="username">
    </div>
    <div class="put">
      <span></span>
      <input type="text" placeholder="" v-model="phone">
    </div>
    <div class="put">
      <span></span>
      <input class="code" type="text" placeholder="" v-model="phone_code">
      <p class="p">
        <span v-if="inentify" @click="send"></span>
        <span v-if="timer">{{count}}s</span>
      </p>
    </div>
    <div class="put">
      <span></span>
      <input type="password" placeholder="" v-model="password">
    </div>
    <!-- <div class="put">
      <span></span>
      <input type="password" placeholder="" v-model="password_confirmation">
    </div>-->
    <div class="put">
      <span></span>
      <input type="text" placeholder="" v-model="recommend">
    </div>
    <button @click="register"></button>
  </div>
</template>

<script>
export default {
  name: "marketfoot",
  data() {
    return {
      username: "",
      phone: "",
      password: "",
      password_confirmation: "",
      security: "",
      security_confirmation: "",
      phone_code: "",
      recommend: "",
      inentify: true,
      timer: false,
      count: 60,
      time: ""
    };
  },
  methods: {
    //    1
    async send() {
      if (!this.phone) {
        this.$toasted.error("", { icon: "error" }).goAway(2000);
        return;
      }
      try {
        // await await user is undefined 
        let res = await this.http.post("/api/send_code", {
          phone: this.phone
        });
        console.log(res);
        this.$toasted.success("").goAway(1500);
      } catch (error) {
        this.$toasted.error(error.message, { icon: "error" }).goAway(2000);
      }
      (this.inentify = !this.inentify),
        (this.timer = true),
        (this.time = setInterval(() => {
          this.count -= 1;
          if (this.count == 0) {
            clearInterval(this.time);
            this.inentify = !this.inentify;
            this.timer = false;
            this.count = 60;
          }
        }, 1000));
    },
    async register() {
      if (
        !this.phone ||
        !this.username ||
        !this.password ||
        // !this.password_confirmation ||
        !this.phone_code ||
        !this.recommend
      ) {
        this.$toasted.error("", { icon: "error" }).goAway(2000);
        return;
      }

      try {
        // await await user is undefined 
        let res = await this.http.post("/api/register", {
          nick_name: this.username,
          phone: this.phone,
          password: this.password,
          // password_confirmation: this.password_confirmation,
          phone_code: this.phone_code,
          recommend: this.recommend
        });
        console.log(res);
        this.$toasted.success("").goAway(1500);
        this.$router.replace({ name: "login" });
      } catch (error) {
        this.$toasted.error(error.message, { icon: "error" }).goAway(2000);
      }
    }
  }
};
</script>

